/* General Styles */
body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f4f4f9;
    color: #333;
  }
  
  h1.heading {
    text-align: center;
    margin: 20px 0;
    font-size: 2.5rem;
    color: #0078d7;
  }
  
  /* Main Container */
  .main-contaner {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 20px;
  }
  
  .main-box {
    background: #f002d0a8;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    width: 100%;
    max-width: 500px;
  }
  
  /* Question Section */
  .question-and-count-section {
    margin-bottom: 20px;
  }
  
  .count-section {
    font-size: 1rem;
    margin-bottom: 10px;
    font-weight: bold;
    color: #6c757d;
  }
  
  .question-section {
    font-size: 1.2rem;
    font-weight: bold;
    color: #343a40;
  }
  
  /* Answer Section */
  .answer-section button {
    display: block;
    width: 100%;
    margin: 10px 0;
    padding: 10px;
    font-size: 1rem;
    background: #0078d7;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background 0.3s ease;
  }
  
  .answer-section button:hover {
    background: #005bb5;
  }
  
  .answer-section button:active {
    transform: scale(0.98);
  }
  
  /* Score Display */
  .show-score {
    background: rgba(191, 233, 5, 0.541);
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    text-align: center;
    width: 100%;
    max-width: 500px;
  }
  
  .show-score p {
    font-size: 1.2rem;
    margin: 10px 0;
  }
  
  .Correct-Answers {
    color: #28a745;
    font-weight: bold;
  }
  
  .Correct-Answers1 {
    color: #17a2b8;
    font-weight: bold;
  }
  
  .Wrong-Answers {
    color: #dc3545;
    font-weight: bold;
  }
  
  .Medium {
    color: #ffc107;
    font-weight: bold;
  }
  
  .verygood {
    color: #0078d7;
    font-weight: bold;
    font-size: 1.5rem;
  }
  
  /* Media Query for Mobile Devices */
  @media (max-width: 768px) {
    .main-box, .show-score {
      padding: 15px;
    }
  
    h1.heading {
      font-size: 2rem;
    }
  
    .question-section {
      font-size: 1rem;
    }
  
    .answer-section button {
      font-size: 0.9rem;
    }
  }
  